  }
}
@media (max-width: 767px) {
  .membership__wrapper {
    padding: 63px 0 69px 0;
  }
}

.membership__wrapper h2 {
  margin: 0 5px 7px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  font-size: 54px;
  line-height: 64.8px;
  letter-spacing: 1.6px;
  text-align: center;
}
@media (max-width: 1199px) {
  .membership__wrapper h2 {
    margin: 0 5px 7px 0;
    font-size: 42px;
    line-height: 50.4px;
    letter-spacing: 1.3px;
  }
