  position: relative;
  display: flex;
  gap: 6px;
  margin: 0 auto 39px auto;
  width: max-content;
}
.membership__controls::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 18px;
  width: calc(100% - 37px);
  height: 2px;
  background-color: rgba(120, 120, 120, 0.2);
}
@media (max-width: 1199px) {
  .membership__controls {
    gap: 15px;
    margin: 0 auto 45px auto;
  }
}
@media (max-width: 767px) {
  .membership__controls {
    margin: 0 auto 34px auto;
  }
}

.membership__controls button {
  position: relative;
  display: inline-block;
  padding: 10px 20px 12px 20px;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  color: rgba(16, 38, 84, 0.5);
  background-color: transparent;
  border: none;
  cursor: pointer;
}
@media (max-width: 1199px) {
  .membership__controls button {
    padding: 10px 20px 13px 20px;
    font-size: 18px;
    line-height: 27px;
  }
}
