  .membership__controls button {
    padding: 10px 15px 14px 15px;
    font-size: 16px;
    line-height: 24px;
  }
}
.membership__controls button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 15%;
  width: 70%;
  height: 2px;
  background-color: #d10a42;
  transform: scale(0);
  transform-origin: center;
  transition: 0.3s;
}
.membership__controls button:hover {
  color: #d10a42;
}

.membership__controls button.is-active {
  color: #102654;
}
.membership__controls button.is-active::before {
  transform: scale(1);
}

.membership__lists {
  position: relative;
  z-index: 1;
}
.membership__lists::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 15%;
  height: 100%;
  background-color: #ffffff;
}
